.header-container {
  height: 230px;
  width: 100%;
  background-color: #000;
  background-image: linear-gradient(90deg, rgba(0, 0, 0, .56), rgba(0, 0, 0, .56)), url("header-pic-1024.82af912b.jpg");
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 30px 70px 97px;
}

.container-divisions {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 27px;
  display: flex;
}

.container-divisions .division-left {
  align-items: center;
  gap: 12px;
  display: flex;
}

.container-divisions .division-right {
  margin: 0;
}

.main_tittle {
  color: #fff;
  text-decoration: none;
}

.logo-icon {
  display: flex;
}

.logo-icon .film-icon {
  fill: rgba(0, 0, 0, 0);
  cursor: pointer;
  stroke: #ff4500;
  text-shadow: 0 0 15px #ff001b;
}

.page-name {
  color: #fff;
  font-size: 20px;
  line-height: 35.16px;
}

.page-name:hover, .page-name:focus {
  cursor: pointer;
  color: #ff4500;
  stroke: #ff001b;
  text-shadow: 0 0 15px #ff001b;
  transition: all .3s;
}

.menu-home {
  justify-content: flex-end;
  display: flex;
}

.menu-home__item {
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  position: relative;
}

.menu-home__item:before {
  content: "";
  width: 90%;
  opacity: 0;
  border-bottom: 2px solid #ff4500;
  transition: all .3s;
  position: absolute;
  bottom: -3px;
  right: 0;
}

.menu-home__item.is-active:before, .menu-home__item:hover:before {
  width: 70%;
  opacity: 1;
}

.menu-home__item:after {
  content: "";
  width: 70%;
  opacity: 0;
  border-bottom: 2px solid red;
  transition: all .3s;
  position: absolute;
  bottom: -6px;
  left: 0;
}

.menu-home__item.is-active:after, .menu-home__item:hover:after {
  width: 50%;
  opacity: 1;
}

.menu-home__item:not(:last-child) {
  margin-right: 39px;
}

.menu-home__btn {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  padding: 1px 0;
}

.menu-home__link {
  text-transform: uppercase;
  color: #fff;
  background: none;
  border: none;
  padding: 1px 0;
  text-decoration: none;
}

.division-center form {
  max-width: 366px;
  border-bottom: 1px solid #fff;
  margin-left: auto;
  margin-right: auto;
}

.search-input {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-input input {
  width: 100%;
  color: #fff;
  background: none;
  border: none;
}

.search-input input:focus {
  outline: none;
}

.search-input input:focus-within {
  border-bottom: 1px solid #ff001b;
  border-radius: 6px;
  box-shadow: 0 0 15px #ff4500;
}

.scoop-icon-btn {
  cursor: pointer;
  background: none;
  border: none;
}

.scoop-icon-btn svg {
  fill: rgba(0, 0, 0, 0);
  stroke: #818181;
}

.scoop-icon-btn:focus, .scoop-icon-btn:hover {
  border-bottom: 1px solid #ff001b;
  border-radius: 6px;
  box-shadow: 0 0 15px #ff4500;
}

.switch-button {
  cursor: pointer;
  justify-content: flex-end;
  display: flex;
}

.switch-button__label {
  width: 30px;
  height: 16px;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #ff6b08;
  border-radius: 30px;
  display: inline-block;
  position: relative;
}

.switch-button__label:before {
  width: 10px;
  height: 10px;
  content: "";
  box-shadow: inset 0px 0px 0px 1px var(--color-black);
  background-color: #ff6b08;
  border-radius: 50%;
  margin: 1px;
  transition: all .2s;
  display: block;
  position: absolute;
}

.switch-button__checkbox:checked + .switch-button__label {
  background-color: gray;
  border-color: #545454;
}

.switch-button__checkbox:checked + .switch-button__label:before {
  background-color: #000;
  transform: translateX(1rem);
}

.switch-button__checkbox {
  display: none;
}

